.texts {
    margin-top: 24px;
}

.mainTitle {
    font-weight: 600;
    font-size: 22px;
    line-height: 28px;
    color: #FFFFFF;
    cursor: pointer;
}

.additionalTitle {
    margin-top: 16px;
    font-weight: 300;
    font-size: 18px;
    line-height: 26px;
    color: #8BACD9;
}

.seconderyContent {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
    font-size: 16px;
    line-height: 20px;
}

.seconderyContent::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    margin: 0 auto;
    background-color: #2E405A;
    top: 40px;
}

.seconderyContentEthereum {
    display: flex;
    align-items: center;
    gap: 6.50px;
    font-weight: 600;
    color: #00FFF8;
}

.seconderyContentClock {
    display: flex;
    align-items: center;
    gap: 6.75px;
    margin-top: 2px;
    font-weight: 400;
    color: #8BACD9;
}