.mainContainer {
    margin-top: 38px;
    display: flex;
    max-height: 100%;
    align-items: center;
}

.mainContainer img {
    border-radius: 50%;
    border: 2px solid white;
    width: 33px;
    height: 33px;
    display: flex;
}

.mainContainer p {
    display: flex;
    font-size: 16px;
    line-height: 20px;
    color: #8BACD9;
    padding-left: 16px;
}

.mainContainer a {
    display: flex;
    text-decoration: none;
    padding-left: 5px;
    color: #FFFFFF;
    cursor: pointer;
    overflow: hidden;
}